:host {
  display: block;
}
.badge-group {
  padding: 4px 10px 4px 4px;
  border-radius: min(calc(var(--radius, 8px)), 16px);
  display: flex;
  gap: 8px;
  align-items: center;
}
.badge {
  padding: 2px 8px;
  border-radius: min(calc(var(--radius, 8px)), 16px);
  border-width: 1px;
  border-style: solid;
  font-size: 12px;
  line-height: 18px;
  background: white;
  width: fit-content;
  display: none;

  /* margin-right: 8px; */
}
.badge-group.error {
  color: var(--error-700, #b42318);
  background: var(--error-50, #fef3f2);
}

.badge-group.error .badge {
  border-color: var(--error-200, #fecdca);
}
.badge-group.pri {
  color: var(--error-700, #b42318);
  background: var(--error-50, #fef3f2);
}

.badge-group.error .badge {
  border-color: var(--error-200, #fecdca);
}
.message {
  flex: 1;
  text-align: center;
  /* margin-inline-start: 3rem; */
}
@media (min-width: 768px) {
  .badge {
    display: block;
    position: absolute;
  }
}
